import { createStore } from 'vuex'
import FirebaseApp from "../firebaseConfig"
import { getFirestore, collection, query, orderBy, onSnapshot, addDoc, deleteDoc, doc, updateDoc} from "firebase/firestore";

let db = getFirestore(FirebaseApp);

export default createStore({
  state: {
    usuarios: []
  },
  getters: {
  },
  mutations: {
    SET_USERS(state, usuarios){
      state.usuarios = usuarios
    },
    DEL_USERS(state, id) {
      let index = state.usuarios.findIndex(usuario => usuario.id == id)
      state.usuarios.splice(index, 1)
    },
    UPDATE_USER(state, editUser) {
      let index = state.usuarios.findIndex(usuario => usuario.id == editUser.id);
      if (index !== -1) {
        state.usuarios[index] = editUser;
      }
    }
  },
  actions: {
    async setUsers({commit}){
      try {
        let userRef = collection(db, "usuarios");
        const queryData = query(userRef, orderBy("nombre", "asc"))
        onSnapshot(queryData, (snapshot) => {
          let usuarios = snapshot.docs.map((doc) => ({id: doc.id, ...doc.data()}));
          commit("SET_USERS", usuarios)
        })
      } catch (error) {
        console.log(error)
      }
    },
    async addUser(context, nuevoUsuario) {
      try {
       
        let userRef = collection(db, 'usuarios')
        let usuario = {
          nombre: nuevoUsuario.nombre,
          email: nuevoUsuario.email,
          
        }
        await addDoc(userRef, usuario);
        return true
      } catch (error) {
        console.log(error)
        return false
      }
    },
    async delUser(context, userID) {
      try {
        let confirmDel = confirm("Desea eliminar el usuario: " + userID + "?");
        if (!confirmDel) {
          return
        }
        const usersRef = doc(db, 'usuarios', userID)
        await deleteDoc(usersRef)
        alert("Usuario eliminado correctamente.")
      } catch (error) {
        console.log(error)
        alert("Error al intentar eliminar el usuario.")
      }
    },
    async updateUser({ commit }, editUser) {
      try {
        let userRef = doc(db, 'usuarios', editUser.id);
        await updateDoc(userRef, {
          nombre: editUser.nombre,
          email: editUser.email
        });
        commit('UPDATE_USER', editUser);
        alert('Usuario actualizado correctamente.');
      } catch (error) {
        console.log(error);
        alert('Error al intentar editar el usuario.')
      }
    }
  },
  modules: {
  }
})
